.theme-light{
  background-color:#fff;
  background:#fff;
  -webkit-text-fill-color:#000;
  color:#000;
  font-weight:bolder;
}


.theme-dark{
  background-color:#000;
  background:#000;
  -webkit-text-fill-color:#fff;
  color:#fff;
  font-weight:bolder;
}


.theme-comp-light,.theme-comp-light:hover,.theme-comp-light:focus{
  background-color:#0d0d0d;
  background:#0d0d0d;
  -webkit-text-fill-color:#fff;
  color:#fff;
  font-weight:bolder;
}

.theme-comp-dark,.theme-comp-dark:hover,.theme-comp-dark:focus{
  background-color:#333;
  background:#333;
  -webkit-text-fill-color:#fff;
  color:#fff;
  font-weight:bolder;
}





.theme-comp2-light,.theme-comp2-light:hover,.theme-comp2-light:focus{
  border:none;
  background-color:transparent;
  background:transparent;
  -webkit-text-fill-color:#000;
  color:#000;
  font-weight:bolder;
}

.theme-comp2-dark,.theme-comp2-dark:hover,.theme-comp2-dark:focus{
  border:none;
  background-color:transparent;
  background:transparent;
  -webkit-text-fill-color:#fff;
  color:#fff;
  font-weight:bolder;
}


/*.theme-txt-light, */


.theme-txt{
  -webkit-text-fill-color:#fff;
  color:#fff;
}
.theme-dark small{
  -webkit-text-fill-color:#fff;
  color:#fff;
  text-shadow:0 0 1px #fff;
  font-weight:bolder;
}

.theme-light small{
  -webkit-text-fill-color:#000;
  color:#000;
  text-shadow:0 0 1px #000;
  font-weight:bolder;
}

.theme-dark .fas{
  text-shadow:0 0 1.5px #fff;
}

.theme-light .fas{
  text-shadow:0 0 1.5px #000;
}

.theme-light-bulb{
-webkit-text-fill-color:yellow;
color:yellow;
animation:glow .5s linear infinite;
}



@keyframes glow{
  from{
    text-shadow:0 0 8px #000;
  }

  to{

    text-shadow:0 0 3px #000;
  }
}



